import React, { useContext } from 'react'

import './styles.css'

import { MdDeleteForever, MdModeEdit } from 'react-icons/md'

import ContextStore from '../../Store/ContextStore'
import firebase from '../../firebase'

const TableUsers = () => {
	const { users, editRow, setModal, hide } = useContext(ContextStore)

	const onDelete = (id, name) => {
		if (window.confirm(`Deseja realmente deletar ${name}?`)) {
			const db = firebase.firestore()

			db.collection('users').doc(id).delete()
		}
	}

	const action = user => {
		editRow(user)
		setModal('edit')
	}

	const totalPoints = () => {
		return users.reduce((a, b) => a + b.points, 0)
	}

	return (
		<div className="wapper">
			{!hide.hidden ? (
				<table className="table-users">
					<thead>
						<tr>
							<th>#</th>
							<th>Nome</th>
							<th>Pontos</th>
							<th>Pulos</th>
							<th>%</th>
							<th>Ações</th>
						</tr>
					</thead>
					<tbody>
						{users.length > 0 ? (
							users.sort((a, b) => b.points - a.points).map((user, index) => (
								<tr
									key={user.id}
									style={
										user.jumps > 0 ? (
											{ background: '#dff0d8', fontWeight: '400' }
										) : (
											{ background: '#f2dede', fontWeight: '300' }
										)
									}
								>
									<td>{index + 1}°</td>
									<td>{user.name}</td>
									<td>{user.points.toLocaleString('pt-BR')}</td>
									<td>{user.jumps}</td>
									<td>{(user.points * 100 / totalPoints()).toFixed(2)}%</td>

									<td>
										<div className="button-group">
											<button
												onClick={() => action(user)}
												className="btn-table btn-edit"
											>
												<MdModeEdit />
											</button>
											<button
												onClick={() => onDelete(user.id, user.name)}
												className="btn-table btn-del"
											>
												<MdDeleteForever />
											</button>
										</div>
									</td>
								</tr>
							))
						) : (
							<tr>
								<td colSpan="6">Nenhum player encontrado</td>
							</tr>
						)}
					</tbody>
				</table>
			) : (
				<table className="table-users">
					<thead>
						<tr>
							<th>#</th>
							<th>Nome</th>
							<th>Pulos</th>
						</tr>
					</thead>
					<tbody>
						{users.length > 0 ? (
							users.sort((a, b) => b.jumps - a.jumps).map((user, index) => (
								<tr
									key={user.id}
									style={
										user.jumps > 0 ? (
											{ background: '#dff0d8', fontWeight: '400' }
										) : (
											{ background: '#f2dede', fontWeight: '300' }
										)
									}
								>
									<td>{index + 1}°</td>
									<td>{user.name}</td>
									<td>{user.jumps}</td>
								</tr>
							))
						) : (
							<tr>
								<td colSpan="6">Nenhum player encontrado</td>
							</tr>
						)}
					</tbody>
				</table>
			)}
		</div>
	)
}

export default TableUsers
