import React, { useState } from 'react'
import { MdMenu } from 'react-icons/md'

import logo from '../assets/imgs/logo192.png'

import './styles.css'

const Header = () => {
	const [ menu, setMenu ] = useState(false)

	const onMenu = () => {
		if (menu) {
			setMenu(false)
		} else if (!menu) {
			setMenu(true)
		}
	}

	const construction = () => {
		alert('Aplicação em desenvolvimento - by: BrunoL')
	}

	return (
		<div className="container-header">
			<header>
				<div className="logo">
					<img src={logo} alt="" />
					<p>MoWᴵᴷᴵ</p>
				</div>

				<div className="menu" onClick={() => construction()}>
					<MdMenu />
				</div>
			</header>

			<nav
				className="nav-menu"
				style={
					menu ? (
						{ left: '0px', transition: '.5s' }
					) : (
						{ left: '-100%', transition: '.5s' }
					)
				}
			>
				<ul>
					<li>
						<a href="/#">Inicio</a>
					</li>
					<li>
						<a href="/#">Regras</a>
					</li>
					<li>
						<a href="/#">Tabela</a>
					</li>
				</ul>
			</nav>
		</div>
	)
}

export default Header
